.bg{
    position: fixed;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0, 0,0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.container{
    width: 600px;
    height: 400px;
    background-color:white;
}

.text-bar{
    padding: 50px 20px 50px 20px;
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
}

.button-bar{
    width: 100%;
    height: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
}

